import React from "react";
import "./App.css";
import {
  Col,
  Row,
  Form,
  InputNumber,
  Button,
  Radio,
  notification,
  Card,
  Skeleton,
  Drawer,
  Timeline,
  Popconfirm,
} from "antd";
import GameStatus from "./components/GameStatus";
import OrderList from "./components/OrderList";
import setting from "./data/setting";
import MarketView from "./components/MarketView";
import AnimationConfig from "./components/AnimationConfig";

function _N(n) {
  n = (n * 100).toFixed() / 100;
  return n.toLocaleString();
}

function _C(n, prev = undefined) {
  if (prev === undefined) {
    return "$" + _N(n);
  } else {
    return "$" + _N(prev) + " -> $" + _N(n);
  }
}

function _U(n, prev = undefined) {
  if (prev === undefined) {
    return _N(n) + "kg";
  } else {
    return _N(prev) + "kg -> " + _N(n) + "kg";
  }
}

const defaultGameStore = {
  rounds: [
    {
      roundIdx: -1,

      wallet: setting.initWallet,
      resilientLevel: 1,
      cattleInventory: 0,
      productInventory: setting.initInventory,

      fixCostPerRound: setting.fixCostPerRound,
      processingCapacity: setting.processingCapacity,
      processingCost: setting.processingCost,
      processingOverloadCost: setting.processingOverloadCost,

      inventoryCapacity: setting.inventoryCapacity,
      inventoryCost: setting.inventoryCost,
      inventoryOverloadCost: setting.inventoryOverloadCost,

      resilientLevelUpgraded: 1,

      step: 8,
      upgradeCostL2: setting.upgradeCostL2,
      upgradeCostL3: setting.upgradeCostL3,

      pExpansionCostPerkg: setting.pExpansionCostPerkg,
      pExpansionEffective: setting.pExpansionEffective,

      iExpansionCostPerkg: setting.iExpansionCostPerkg,
      iExpansionEffective: setting.iExpansionEffective,

      pExpansions: [],
      iExpansions: [],

      messages: [],
      stepMessage: "",

      orders: [],
      markets: [],

      roundSetting: {
        texasPurchasingPrice: 0,
        californiaPurchasingPrice: 0,
        texasPurchasingShutdown: false,
        californiaPurchasingShutdown: false,
      },

      texasPurchasingAmount: 0,
      californiaPurchasingAmount: 0,

      purchasingEnabled: false,
      riskManagementEnabled: false,

      duration: 5,
      animation: true,
      numOfRound: setting.numOfRound,
    },
  ],
  version: "1.9",
};

class App extends React.Component {
  state = defaultGameStore.rounds[0];

  constructor() {
    super();
    this.processes = [
      this.payFixedCost,
      this.handleEvent,
      this.handleSelling,
      this.handleInbound,
      this.handleProcessing,
      null,
      null,
      this.payInventoryCost,
      this.nextRound,
    ];
  }

  componentDidMount = () => {
    this.loadFromLocalStorage(false);
  };

  loadFromLocalStorage = (reset) => {
    const gameStoreItem = localStorage.getItem("gameStore-cn");

    let gameStore = defaultGameStore;
    if (!reset && gameStoreItem) {
      gameStore = JSON.parse(gameStoreItem);
      if (gameStore.version !== defaultGameStore.version) {
        gameStore = defaultGameStore;
      }
    }
    localStorage.setItem("gameStore-cn", JSON.stringify(gameStore));
    const current = gameStore.rounds.length - 1;
    this.setState(gameStore.rounds[current], this.go);
  };

  saveStateToGameStore = () => {
    const gameStoreItem = localStorage.getItem("gameStore-cn");
    let gameStore = defaultGameStore;
    if (gameStoreItem) {
      gameStore = JSON.parse(gameStoreItem);
    }

    const current = gameStore.rounds.length - 1;
    if (gameStore.rounds[current].roundIdx === this.state.roundIdx) {
      gameStore.rounds.pop();
    }
    gameStore.rounds.push(this.state);
    localStorage.setItem("gameStore-cn", JSON.stringify(gameStore));
  };

  resetGame = () => {
    notification.destroy();
    this.loadFromLocalStorage(true);
  };

  getCurrentRoundSetting = () => {
    const idx = this.state.roundIdx * 3 + this.state.resilientLevel - 1;
    return setting.roundSettings[idx];
  };

  toCurrency = (n) => {
    return "$" + this.toNumber(n);
  };

  toNumber = (n) => {
    n = (n * 100).toFixed() / 100;
    return n.toLocaleString();
  };

  toPercentage = (n) => {
    return this.toNumber(n * 100) + "%";
  };

  nextRound = () => {
    console.log("Next Round");
    this.saveStateToGameStore();

    this.setState(
      {
        roundIdx: this.state.roundIdx + 1,
        processingCapacity: setting.processingCapacity,
        inventoryCapacity: setting.inventoryCapacity,
        resilientLevel: this.state.resilientLevelUpgraded,
        resilientLevelUpgraded: 1,
        step: -1,
        messages: [],
        stepMessage: "",
        messageTitle: "",
        texasPurchasingAmount: 0,
        californiaPurchasingAmount: 0,
        purchasingEnabled: false,
        riskManagementEnabled: false,
        messagesDrawerVisible: false,

        pExpansionAmount: 0,
        iExpansionAmount: 0,
      },
      this.go
    );
  };

  go = () => {
    if (this.state.roundIdx >= setting.numOfRound) {
      notification.open({
        message: "游戏结束",
        description:
          "这是游戏的最后一周，请点击重置游戏按钮，重置游戏",
        duration: 3,
      });
      return;
    }

    if (this.state.wallet < 0) {
      notification.error({
        message: "游戏结束",
        description:
          "噢, 你的资金不足... 生意停止",
        duration: 3,
      });
      return;
    }
    if (this.state.step >= this.processes.length) return;
    if (this.state.step === -1) {
      const roundSetting = this.getCurrentRoundSetting();
      this.setState(
        {
          roundSetting: roundSetting,
          step: this.state.step + 1,
        },
        this.go
      );
    } else {
      const process = this.processes[this.state.step];

      if (this.state.animation && this.state.stepMessage !== "") {
        notification.open({
          message: this.state.messageTitle,
          description: this.state.stepMessage,
          duration: this.state.duration,
          onClose: process,
        });
      } else {
        if (process) process();
      }
    }
  };

  onDurationChanged = (value) => {
    this.setState({
      duration: value,
    });
  };

  payFixedCost = () => {
    let {
      wallet,
      fixCostPerRound,
      step,
      resilientLevel,
      processingCapacity,
      inventoryCapacity,
      pExpansions,
      iExpansions,
      roundIdx,
    } = this.state;
    wallet -= fixCostPerRound;
    let messages = [...this.state.messages];

    messages.push(
      `现金: ${_C(this.state.wallet)}, 库存: ${_U(
        this.state.cattleInventory + this.state.productInventory
      )}`
    );

    let stepMessage = `支付固定费用${_C(fixCostPerRound)}. `;

    if (resilientLevel > 1) {
      stepMessage += `抗风险能力升级到级别${resilientLevel}. `;
    }

    pExpansions.forEach((e) => {
      if (e.effectived <= roundIdx) {
        processingCapacity += e.amount;
      }
      if (e.effectived === roundIdx) {
        stepMessage += `生产能力增加${_U(e.amount)}. `;
      }
    });

    iExpansions.forEach((e) => {
      if (e.effectived <= roundIdx) {
        inventoryCapacity += e.amount;
      }
      if (e.effectived === roundIdx) {
        stepMessage += `库存容量增加${_U(e.amount)}. `;
      }
    });

    messages.push(stepMessage);
    this.setState(
      {
        wallet: wallet,
        messages: messages,
        step: step + 1,
        stepMessage: stepMessage,
        inventoryCapacity,
        processingCapacity,
        messageTitle: `周次${this.state.roundIdx + 1}开始`,
      },
      this.go
    );
  };

  handleEvent = () => {
    const { roundSetting } = this.state;
    let {
      inventoryLoss,
      processingCapacityLoss,
      event,
      texasDeliveryDelay,
      californiaDeliveryDelay,
    } = roundSetting;
    let { roundIdx, productInventory, processingCapacity, step } = this.state;

    let messages = [...this.state.messages];
    let orders = [...this.state.orders];
    let markets = [...this.state.markets];

    let stepMessage = event;

    if (event !== "") {
      messages.push(...event.split("\n"));
    }

    productInventory *= 1 - inventoryLoss;
    processingCapacity *= 1 - processingCapacityLoss;

    if (texasDeliveryDelay > 0) {
      orders.map((order) => {
        if (order.expected >= roundIdx && order.source === "德州") {
          order.expected += texasDeliveryDelay;
        }
        return order;
      });
    }

    if (californiaDeliveryDelay > 0) {
      orders.map((order) => {
        if (order.expected >= roundIdx && order.source === "加州") {
          order.expected += californiaDeliveryDelay;
        }
        return order;
      });
    }

    markets.unshift({
      roundIdx: roundSetting.roundIdx,
      demand: roundSetting.demand,
      sellingPrice: roundSetting.sellingPrice,
    });

    this.setState(
      {
        productInventory: productInventory,
        processingCapacity: processingCapacity,
        step: step + 1,
        messages: messages,
        markets: markets,
        stepMessage: stepMessage,
        messageTitle: "事件",
      },
      this.go
    );
  };

  handleSelling = () => {
    let messages = [...this.state.messages];

    const { roundSetting } = this.state;

    let { demand, sellingPrice } = roundSetting;
    let { productInventory, wallet, step } = this.state;

    let amount = Math.min(productInventory, demand);
    let income = amount * sellingPrice;
    wallet += income;

    let stepMessage = "";
    if (amount > 0) {
      stepMessage = `售出${_U(amount)}. 收入${_C(income)}.`;
      messages.push(stepMessage);
    }

    this.setState(
      {
        productInventory: productInventory - amount,
        wallet: wallet,
        step: step + 1,
        messages: messages,
        stepMessage: stepMessage,
        messageTitle: "销售",
      },
      this.go
    );
  };

  handleInbound = () => {
    let { roundIdx, cattleInventory, step } = this.state;
    let messages = [...this.state.messages];
    let orders = [...this.state.orders];
    let amount = 0;
    orders.map((order) => {
      if (order.expected === roundIdx) {
        amount += order.qty;
        order.received = true;
      }
      return order;
    });
    cattleInventory += amount;

    let stepMessage = "";
    if (amount > 0) {
      stepMessage = `收到${_U(amount)}.`;
      messages.push(stepMessage);
    }

    this.setState(
      {
        step: step + 1,
        messages: messages,
        cattleInventory: cattleInventory,
        stepMessage: stepMessage,
        orders: orders,
        messageTitle: "入库",
      },
      this.go
    );
  };

  handleProcessing = () => {
    let messages = [...this.state.messages];
    const { roundSetting } = this.state;

    let {
      processingCapacity,
      processingCost,
      processingOverloadCost,
      wallet,
      cattleInventory,
      productInventory,
      step,
    } = this.state;

    let { processingShutdown } = roundSetting;

    let stepMessage = "";
    if (processingShutdown) {
      stepMessage = `生产线关闭`;
    } else {
      let totalProcessingCost =
        Math.min(processingCapacity, cattleInventory) * processingCost +
        Math.max(0, cattleInventory - processingCapacity) *
          processingOverloadCost;
      wallet -= totalProcessingCost;
      productInventory += cattleInventory;
      if (cattleInventory > 0) {
        stepMessage = `生产${_U(cattleInventory)}, 费用${_C(
          totalProcessingCost
        )}`;
      }
      cattleInventory = 0;
    }

    if (stepMessage !== "") messages.push(stepMessage);

    console.log("Processing: " + stepMessage);

    this.setState(
      {
        messages: messages,
        cattleInventory: cattleInventory,
        productInventory: productInventory,
        wallet: wallet,
        step: step + 1,
        stepMessage: stepMessage,
        messageTitle: "生产",
      },
      this.go
    );
  };

  onTexasPurchasingAmountChanged = (value) => {
    this.setState({
      texasPurchasingAmount: value,
    });
  };

  onCaliforniaPurchasingAmountChanged = (value) => {
    this.setState({
      californiaPurchasingAmount: value,
    });
  };

  handlePurchasing = () => {
    let messages = [...this.state.messages];

    const { roundSetting } = this.state;
    const {
      californiaPurchasingShutdown,
      texasPurchasingShutdown,
      californiaPurchasingPrice,
      texasPurchasingPrice,
    } = roundSetting;
    let {
      roundIdx,
      wallet,
      californiaPurchasingAmount,
      texasPurchasingAmount,
      step,
    } = this.state;
    let orders = [...this.state.orders];

    let stepMessage = "";
    if (texasPurchasingShutdown) {
      stepMessage += "德州农场关闭. ";
    } else {
      if (texasPurchasingAmount > 0) {
        let order = {
          created: roundIdx,
          source: "德州",
          qty: texasPurchasingAmount,
          price: texasPurchasingPrice,
          expected: roundIdx + 1,
          received: false,
        };
        orders.push(order);
        let orderCost = order.qty * order.price;
        stepMessage += `从德州下单${_U(order.qty)}. 费用${_C(
          orderCost
        )}. `;
        wallet -= orderCost;
      }
    }
    if (californiaPurchasingShutdown) {
      stepMessage += "加州农场关闭.";
    } else {
      if (californiaPurchasingAmount > 0) {
        let order = {
          created: roundIdx,
          source: "加州",
          qty: californiaPurchasingAmount,
          price: californiaPurchasingPrice,
          expected: roundIdx + 1,
          received: false,
        };
        orders.push(order);
        let orderCost = order.qty * order.price;
        stepMessage += `从加州下单${_U(order.qty)}. 费用${_C(
          orderCost
        )}. `;
        wallet -= orderCost;
      }
    }

    if (wallet < 0) {
      notification.error({
        message: "采购",
        description: "资金不足",
        duration: 3,
      });
      return;
    }

    if (stepMessage !== "") {
      messages.push(stepMessage);
      if (this.state.animation) {
        notification.open({
          message: "采购",
          description: stepMessage,
          duration: this.state.duration,
        });
      }
    }
    this.setState(
      {
        orders: orders,
        step: step + 1,
        wallet: wallet,
        messages: messages,
        stepMessage: "",
        messageTitle: "采购",
      },
      this.go
    );
  };

  onResilientLevelUpgradedChanged = (e) => {
    this.setState({
      resilientLevelUpgraded: e.target.value,
    });
  };

  handleResilientLevelUpgrade = () => {
    let {
      wallet,
      upgradeCostL2,
      upgradeCostL3,
      resilientLevelUpgraded,
      pExpansionCostPerkg,
      pExpansionEffective,
      pExpansionAmount,
      iExpansionCostPerkg,
      iExpansionEffective,
      iExpansionAmount,
      step,
      roundIdx,
    } = this.state;
    let messages = [...this.state.messages];
    let pExpansions = [...this.state.pExpansions];
    let iExpansions = [...this.state.iExpansions];

    let stepMessage = "";

    if (resilientLevelUpgraded === 2) {
      wallet -= upgradeCostL2;

      stepMessage = `升级抗风险能力到级别2, 费用${_C(
        upgradeCostL2
      )}. 下一周次生效.`;
    } else if (resilientLevelUpgraded === 3) {
      wallet -= upgradeCostL3;
      stepMessage = `升级抗风险能力到级别3, 费用${_C(
        upgradeCostL3
      )}. 下一周次生效.`;
    }

    if (pExpansionAmount > 0) {
      let cost = pExpansionAmount * pExpansionCostPerkg;
      wallet -= cost;
      stepMessage += `\n 增加生产能力${_U(
        pExpansionAmount
      )}, 每单位费用为${_C(
        pExpansionCostPerkg
      )}, ${pExpansionEffective}周次后生效.`;

      pExpansions.push({
        amount: pExpansionAmount,
        effectived: roundIdx + pExpansionEffective,
      });
    }

    if (iExpansionAmount > 0) {
      let cost = iExpansionAmount * iExpansionCostPerkg;
      wallet -= cost;
      stepMessage += `\n 增加库存容量${_U(
        iExpansionAmount
      )}, 每单位费用为${_C(
        iExpansionCostPerkg
      )}, ${iExpansionEffective}周次后生效.`;
      iExpansions.push({
        amount: iExpansionAmount,
        effectived: roundIdx + iExpansionEffective,
      });
    }

    if (wallet < 0) {
      notification.error({
        message: "风险管理",
        description: "资金不足",
        duration: 3,
      });
      return;
    }

    if (stepMessage !== "") {
      messages.push(stepMessage);
    }
    this.setState(
      {
        messages: messages,
        wallet: wallet,
        stepMessage: stepMessage,
        step: step + 1,
        messageTitle: "风险管理",
        iExpansions,
        pExpansions,
      },
      this.go
    );
  };

  payInventoryCost = () => {
    console.log("Pay Inventory Cost");
    const {
      productInventory,
      cattleInventory,
      inventoryCost,
      inventoryOverloadCost,
      inventoryCapacity,
      step,
    } = this.state;
    let { wallet } = this.state;
    let messages = [...this.state.messages];

    let totalInventory = productInventory + cattleInventory;

    let totalInventoryCost =
      Math.min(inventoryCapacity, totalInventory) * inventoryCost +
      Math.max(0, totalInventory - inventoryCapacity) * inventoryOverloadCost;

    wallet -= totalInventoryCost;

    let stepMessage = "";
    if (totalInventory > 0) {
      stepMessage = `总库存为${_U(totalInventory)}, 库存费用为${_C(
        totalInventoryCost
      )}`;
      messages.push(stepMessage);
    }
    this.setState(
      {
        messages: messages,
        step: step + 1,
        wallet: wallet,
        stepMessage: stepMessage,
        messageTitle: "周次结束",
      },
      this.go
    );
  };

  viewMessages = () => {
    this.setState({
      messagesDrawerVisible: true,
    });
  };

  onMessagesDrawerClosed = () => {
    this.setState({
      messagesDrawerVisible: false,
    });
  };

  onAnimationChanged = (checked) => {
    this.setState({
      animation: checked,
    });
  };

  onPExpansionAmountChanged = (value) => {
    this.setState({
      pExpansionAmount: value,
    });
  };

  onIExpansionAmountChanged = (value) => {
    this.setState({
      iExpansionAmount: value,
    });
  };

  render = () => {
    return (
      <div className="App">
        <div id="header">
          <div className="container">
            <Row>
              <Col span={24} lg={{ span: 12 }}>
                <h1>牛肉加工厂游戏</h1>
              </Col>
              <Col span={0} lg={{ span: 12 }}>
                <div id="header-options">
                  <AnimationConfig
                    animation={this.state.animation}
                    duration={this.state.duration}
                    onAnimationChanged={this.onAnimationChanged}
                    onDurationChanged={this.onDurationChanged}
                  />
                </div>
              </Col>
            </Row>
          </div>
        </div>

        <div id="content">
          <div className="container">
            <Row gutter={{ lg: 20 }}>
              <Col span={24} lg={{ span: 0 }}>
                <Card style={{ marginBottom: 20 }}>
                  <AnimationConfig
                    animation={this.state.animation}
                    duration={this.state.duration}
                    onAnimationChanged={this.onAnimationChanged}
                    onDurationChanged={this.onDurationChanged}
                  />
                </Card>
              </Col>
              <Col span={24} lg={{ span: 10 }}>
                <GameStatus value={this.state} />
                <h3 style={{ marginTop: 20 }}>订单列表</h3>
                <OrderList orders={this.state.orders} />
                <h3 style={{ marginTop: 20 }}>需求和价格</h3>
                <MarketView markets={this.state.markets} />
              </Col>
              <Col span={24} lg={{ span: 14 }}>
                <Card title="消息" style={{ marginBottom: 20 }}>
                  <Timeline>
                    {this.state.messages.map((value) => (
                      <Timeline.Item key={value}>{value}</Timeline.Item>
                    ))}
                  </Timeline>
                </Card>

                {this.state.step !== 5 && this.state.step !== 6 && (
                  <Card title="采购">
                    <Skeleton />
                  </Card>
                )}
                {this.state.step === 5 && this.state.wallet >= 0 && (
                  <Card title="采购">
                    <Form
                      labelCol={{ span: 8 }}
                      size="small"
                      style={{ marginTop: "15px" }}
                    >
                      <Form.Item label="从德州: ">
                        {this.state.roundSetting.texasPurchasingShutdown && (
                          <span>农产关闭</span>
                        )}
                        {!this.state.roundSetting.texasPurchasingShutdown && (
                          <div>
                            <InputNumber
                              size="small"
                              min={0}
                              max={100000}
                              value={this.state.texasPurchasingAmount}
                              onChange={this.onTexasPurchasingAmountChanged}
                            />
                            <span style={{ margin: "10px" }}>
                              {"x " +
                                _C(
                                  this.state.roundSetting.texasPurchasingPrice
                                )}
                            </span>
                          </div>
                        )}
                      </Form.Item>
                      <Form.Item label="从加州: ">
                        {this.state.roundSetting
                          .californiaPurchasingShutdown && (
                          <span>农产关闭</span>
                        )}
                        {!this.state.roundSetting
                          .californiaPurchasingShutdown && (
                          <div>
                            <InputNumber
                              size="small"
                              min={0}
                              max={100000}
                              value={this.state.californiaPurchasingAmount}
                              onChange={
                                this.onCaliforniaPurchasingAmountChanged
                              }
                            />
                            <span style={{ margin: "10px" }}>
                              {"x " +
                                _C(
                                  this.state.roundSetting
                                    .californiaPurchasingPrice
                                )}
                            </span>
                          </div>
                        )}
                      </Form.Item>

                      <Form.Item wrapperCol={{ offset: 8 }}>
                        <Button type="primary" onClick={this.handlePurchasing}>
                          下单
                        </Button>
                      </Form.Item>
                    </Form>
                  </Card>
                )}
                {this.state.step === 6 && this.state.wallet >= 0 && (
                  <Card title="风险管理">
                    <Form
                      labelCol={{ span: 8 }}
                      size="small"
                      style={{ marginTop: "15px" }}
                    >
                      <Form.Item label="风险管理">
                        <Radio.Group
                          value={this.state.resilientLevelUpgraded}
                          onChange={this.onResilientLevelUpgradedChanged}
                        >
                          <Radio
                            value={1}
                            style={{
                              display: "block",
                              height: "30px",
                              lineHeight: "30px",
                            }}
                          >
                            抗风险能力级别1 (费用 $0)
                          </Radio>
                          <Radio
                            value={2}
                            style={{
                              display: "block",
                              height: "30px",
                              lineHeight: "30px",
                            }}
                          >
                            升级抗风险能力到级别2 (费用{" "}
                            {_C(this.state.upgradeCostL2)})
                          </Radio>
                          <Radio
                            value={3}
                            style={{
                              display: "block",
                              height: "30px",
                              lineHeight: "30px",
                            }}
                          >
                            升级抗风险能力到级别3 (费用{" "}
                            {_C(this.state.upgradeCostL3)})
                          </Radio>
                        </Radio.Group>
                      </Form.Item>
                      <Form.Item label="生产能力: ">
                        增加生产能力
                        <InputNumber
                          size="small"
                          min={0}
                          max={100000}
                          style={{ margin: "0px 6px" }}
                          value={this.state.pExpansionAmount}
                          onChange={this.onPExpansionAmountChanged}
                        />
                        , 每单位费用为{_C(this.state.pExpansionCostPerkg)},{" "}
                        {this.state.pExpansionEffective}
                        周次后生效.
                      </Form.Item>
                      <Form.Item label="库存容量: ">
                        增加库存容量
                        <InputNumber
                          size="small"
                          min={0}
                          max={100000}
                          style={{ margin: "0px 6px" }}
                          value={this.state.iExpansionAmount}
                          onChange={this.onIExpansionAmountChanged}
                        />
                        , 每单位费用为{_C(this.state.iExpansionCostPerkg)},{" "}
                        {this.state.iExpansionEffective}
                        周次后生效.
                      </Form.Item>
                      <Form.Item wrapperCol={{ offset: 8 }}>
                        <Button
                          type="primary"
                          onClick={this.handleResilientLevelUpgrade}
                        >
                          确定并进入下一周次
                        </Button>
                      </Form.Item>
                    </Form>
                  </Card>
                )}
                <div style={{ float: "right", marginTop: "20px" }}>
                  <Popconfirm
                    placement="bottom"
                    title="你确定要将游戏重置到周次1吗？"
                    onConfirm={this.resetGame}
                    okText="确定"
                    cancelText="取消"
                  >
                    <Button type="danger">重置游戏</Button>
                  </Popconfirm>
                </div>
              </Col>
            </Row>
          </div>
        </div>
        <div id="footer">
          ©2021 Prof Yanzhi Li
        </div>
        <Drawer
          title="Messages"
          placement="right"
          closable={false}
          onClose={this.onMessagesDrawerClosed}
          visible={this.state.messagesDrawerVisible}
          width={500}
        >
          <Timeline>
            {this.state.messages.map((value) => (
              <Timeline.Item key={value}>{value}</Timeline.Item>
            ))}
          </Timeline>
          ,
        </Drawer>
      </div>
    );
  };
}

export default App;
