import React from "react";
import { Table } from "antd";

class OrderList extends React.Component {
  rowClassName = (record, index) => {
    if (record.received) {
      return "received-order";
    }
    return "pendding-order";
  };

  render = () => {
    const { orders } = this.props;

    const columns = [
      {
        title: "下单周次",
        dataIndex: "created",
        key: "created",
        render: (text, record) => "R" + (record.created + 1),
      },
      {
        title: "来源",
        dataIndex: "source",
        key: "source",
      },
      {
        title: "数量",
        dataIndex: "qty",
        key: "qty",
        render: (text, record) => text.toLocaleString(),
      },
      {
        title: "价格",
        dataIndex: "price",
        key: "price",
        render: (text, record) => text.toLocaleString(),
      },
      {
        title: "到货周次",
        dataIndex: "expected",
        key: "expected",
        render: (expected, record) =>
          record.received ? "到货" : "R" + (record.expected + 1),
      },
    ];

    orders.sort((a, b) => {
      return b.expected - a.expected;
    });

    return (
      <Table
        bordered
        dataSource={orders}
        size="small"
        rowClassName={this.rowClassName}
        columns={columns}
        pagination={{ pageSize: 5 }}
      />
    );
  };
}

export default OrderList;
