import React from "react";
import TweenOne from "rc-tween-one";
import Children from "rc-tween-one/lib/plugin/ChildrenPlugin";

TweenOne.plugins.push(Children);

const StatusItem = ({ title, value, prefix, suffix }) => {
  const animation = {
    Children: {
      value: value,
      formatMoney: true,
      floatLength: 0,
    },
    duration: 1000,
  };
  return (
    <div className="ant-statistic">
      <div className="ant-statistic-title">{title}</div>
      <div className="ant-statistic-content">
        <span className="ant-statistic-content-prefix">{prefix}</span>
        <span className="ant-statistic-content-value">
          <TweenOne animation={animation}>0</TweenOne>
        </span>
        <span className="ant-statistic-content-suffix">{suffix}</span>
      </div>
    </div>
  );
};

export default StatusItem;
