import React from "react";

import { Button, Modal, Row, Col, Select } from "antd";
import HistoryView from "./HistoryView";

const { Option } = Select;

class HistoryViewButton extends React.Component {
  state = {
    roundIdx: 0,
    wallet: 0,
    resilientLevel: 1,
    cattleInventory: 0,
    productInventory: 0,
    californiaOrdered: 0,
    texasOrdered: 0,
    numOfRound: 0,
    messages: [],
    historyViewVisible: false,
  };

  gameStore = { rounds: [{}] };

  loadFromLocalStorage = () => {
    const gameStoreItem = localStorage.getItem("gameStore-cn");
    if (gameStoreItem) {
      this.gameStore = JSON.parse(gameStoreItem);
    }
    this.loadRoundInfo(this.gameStore.rounds.length - 1);
  };

  loadRoundInfo = (idx) => {
    if (idx >= this.gameStore.rounds.length || idx === 0) return;
    const {
      roundIdx,
      wallet,
      resilientLevel,
      cattleInventory,
      productInventory,
      numOfRound,
      messages,
      orders,
    } = this.gameStore.rounds[idx];

    let californiaOrdered = 0;
    let texasOrdered = 0;

    orders.forEach((order) => {
      if (!order.received) {
        if (order.source === "Texas") texasOrdered += order.qty;
        else californiaOrdered += order.qty;
      }
    });

    this.setState({
      roundIdx,
      wallet,
      resilientLevel,
      cattleInventory,
      productInventory,
      numOfRound,
      messages,
      californiaOrdered,
      texasOrdered,
    });
  };

  onRoundChanged = (value) => {
    this.loadRoundInfo(value);
  };

  closeHistoryView = () => {
    this.setState({
      historyViewVisible: false,
    });
  };

  openHistoryView = () => {
    this.loadFromLocalStorage();
    this.setState({
      historyViewVisible: true,
    });
  };

  onClickViewHistory = () => {};
  render = () => {
    return (
      <div>
        <Button type="default" size="small" onClick={this.openHistoryView}>
          历史记录
        </Button>
        <Modal
          title="历史记录"
          visible={this.state.historyViewVisible}
          onCancel={this.closeHistoryView}
          onOk={this.closeHistoryView}
          width={800}
          footer={
            <Row>
              <Col span={12}>
                <div style={{ float: "left" }}>
                  <span>周次:</span>
                  <Select
                    size="small"
                    value={this.state.roundIdx + 1}
                    style={{ width: 60, marginLeft: 10, marginRight: 10 }}
                    onChange={this.onRoundChanged}
                  >
                    {this.gameStore.rounds.map((value, index) => {
                      if (index > 0)
                        return <Option value={index}>{index}</Option>;
                      else return "";
                    })}
                  </Select>
                </div>
              </Col>
              <Col span={12}>
                <div style={{ float: "right" }}>
                  <Button type="primary" onClick={this.closeHistoryView}>
                    关闭
                  </Button>
                </div>
              </Col>
            </Row>
          }
        >
          <HistoryView value={this.state} />
        </Modal>
      </div>
    );
  };
}

export default HistoryViewButton;
