import React from "react";
import { Col, Row, Card } from "antd";
import StatusItem from "./StatusItem";
import HistoryViewButton from "./HistoryViewButton";

class GameStatus extends React.Component {
  render = () => {
    const {
      roundIdx,
      wallet,
      resilientLevel,
      cattleInventory,
      productInventory,
      inventoryCapacity,
      processingCapacity,
      numOfRound,
    } = this.props.value;
    return (
      <Card>
        <Row gutter={[16, 20]}>
          <Col span={12}>
            <StatusItem
              title="当前周次"
              value={roundIdx + 1}
              prefix="周次 "
              suffix={"/ " + numOfRound}
            />
          </Col>
          <Col span={12}>
            {roundIdx > 0 && (
              <div style={{ marginTop: 36 }}>
                <HistoryViewButton />
              </div>
            )}
          </Col>
        </Row>
        <Row gutter={[16, 20]}>
          <Col span={12}>
            <StatusItem title="现金 (USD)" value={wallet} prefix="$" />
          </Col>
          <Col span={12}>
            <StatusItem
              title="抗风险能力"
              value={resilientLevel}
              prefix="级别"
            />
          </Col>
        </Row>
        <Row gutter={[16, 20]}>
          <Col span={12}>
            <StatusItem
              title="牛库存"
              value={cattleInventory}
              suffix="kg"
            />
          </Col>
          <Col span={12}>
            <StatusItem
              title="产品库存"
              value={productInventory}
              suffix="kg"
            />
          </Col>
        </Row>
        <Row gutter={[16, 20]}>
          <Col span={12}>
            <StatusItem
              title="库存容量"
              value={inventoryCapacity}
              suffix="kg"
            />
          </Col>
          <Col span={12}>
            <StatusItem
              title="生产能力"
              value={processingCapacity}
              suffix="kg"
            />
          </Col>
        </Row>
      </Card>
    );
  };
}

export default GameStatus;
