import React from "react";
import { Row, Col, Card, Timeline } from "antd";
import StatusItem from "./StatusItem";
class HistoryView extends React.Component {
  render = () => {
    const {
      roundIdx,
      wallet,
      resilientLevel,
      cattleInventory,
      productInventory,
      californiaOrdered,
      texasOrdered,
      numOfRound,
      messages,
    } = this.props.value;

    return (
      <div>
        <Row gutter={{ lg: 20 }}>
          <Col span={24} lg={{ span: 12 }}>
            <Card style={{ marginBottom: 30 }}>
              <Row gutter={[16, 20]}>
                <Col span={12}>
                  <StatusItem
                    title="当前周次"
                    value={roundIdx + 1}
                    prefix="周次 "
                    suffix={"/ " + numOfRound}
                  />
                </Col>
              </Row>
              <Row gutter={[16, 20]}>
                <Col span={12}>
                  <StatusItem title="现金 (USD)" value={wallet} prefix="$" />
                </Col>
                <Col span={12}>
                  <StatusItem
                    title="抗风险能力"
                    value={resilientLevel}
                    prefix="级别"
                  />
                </Col>
              </Row>
              <Row gutter={[16, 20]}>
                <Col span={12}>
                  <StatusItem
                    title="牛库存"
                    value={cattleInventory}
                    suffix="kg"
                  />
                </Col>
                <Col span={12}>
                  <StatusItem
                    title="产品库存"
                    value={productInventory}
                    suffix="kg"
                  />
                </Col>
              </Row>
              <Row gutter={[16, 20]}>
                <Col span={12}>
                  <StatusItem
                    title="德州订单"
                    value={texasOrdered}
                    suffix="kg"
                  />
                </Col>
                <Col span={12}>
                  <StatusItem
                    title="加州订单"
                    value={californiaOrdered}
                    suffix="kg"
                  />
                </Col>
              </Row>
            </Card>
          </Col>
          <Col span={24} lg={{ span: 12 }}>
            <h3 style={{ marginBottom: 30 }}>消息</h3>
            <Timeline>
              {messages.map((value) => (
                <Timeline.Item>{value}</Timeline.Item>
              ))}
            </Timeline>
          </Col>
        </Row>
        <div></div>
      </div>
    );
  };
}

export default HistoryView;
