import React from "react";
import { Switch, Select } from "antd";
const { Option } = Select;

class AnimationConfig extends React.Component {
  render = () => {
    const {
      animation,
      duration,
      onAnimationChanged,
      onDurationChanged,
    } = this.props;
    return (
      <div>
        <span>动画</span>
        <Switch
          checkedChildren={<span style={{ paddingLeft: 5 }}>开</span>}
          unCheckedChildren={<span style={{ paddingRight: 5 }}>关</span>}
          checked={animation}
          size="small"
          onChange={onAnimationChanged}
          style={{ marginLeft: 10, marginRight: 10 }}
        />
        {animation && (
          <Select
            size="small"
            value={duration + "s"}
            style={{ width: 60, marginLeft: 10, marginRight: 10 }}
            onChange={onDurationChanged}
          >
            <Option value={5}>5s</Option>
            <Option value={3}>3s</Option>
            <Option value={1}>1s</Option>
          </Select>
        )}
      </div>
    );
  };
}

export default AnimationConfig;
